.aboutHome{
    height:auto;
    padding: 20px 0;
    display: block;
}
.aboutHome #heading{
    text-align: left;
    padding: 0;
}
.ritems{
  display: block;
}
.rcontainer{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(17rem, auto));
    align-items: center;
    gap: 1.5rem;
    padding: 20px;
}
.ritem {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(17rem, auto));
    align-items: center;
    gap: 1.5rem;
}
.rleft img{
    width:100%;
}

.awrapper{
    background-image: url("../../../public/wrapper.jpeg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: cover;
    color:#fff;
    text-align: center;
}
.awrapper .box{
    padding: 35px 0;
}
.awrapper i{
    margin-right: 30px;
    font-size: 68px;
}
.awrapper h1{
    font-size: 32px;
    color:#1554dd;
    text-align: center;
}
.awrapper h3{
    font-size: 20px;
    font-weight: 500;
}
@media screen and (max-width:892px) {
    .aboutHome{
        margin-top: 72%;
        padding: 0;
    }
    .awrapper{
        height: cover;
    }
    .rcontainer{
        display: block;
    }    
    .awrapper i{
       font-size: 50px;
   }
   .awrapper .flex{
    background-color: rgba(255, 255, 255, 0.303);
   }
   .awrapper .flex:hover {
       background-color: rgba(189, 210, 239, 0.731)
   }
}
@media screen and (max-width:632px) {
   .awrapper .grid{
    grid-template-columns: repeat(1, 1fr);
    row-gap: 0;
   }
   .awrapper .flex .rcontainer{
       display: block;
       width:100%;
   }
   .flex{
       border-radius: 0;
       /* border-top: 1px solid; */
   }    
}