.testimonal{
    justify-content: center;
    background-image: url(../../../../public/testback.jpg);
}
.testimonal .items{
padding: 20px;
transition: 0.5s;
margin-bottom: 48px;
cursor: pointer;
}
.swiper {
    width: 100%;
    height: 100%;
}

.swiper-slide {
    text-align: center;
    font-size: 18px;

    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
}
.testimonal .img{
    position: relative;
    left: 0;
}
.testimonal img {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 2px solid #1554dd;
    padding: 3px;
}
.testimonal i {
    position: absolute;
    bottom: 0;
    left: 60px;
    background-color: #1554dd;
    color: #fff;
}
.testimonal .name{
    margin: 25px;
}
.testimonal .name h2{
    font-size: 24px;
    margin-bottom: 4px;
    text-align: left;
}
.testimonal .name span{
    color: #1554dd;
    text-align: left;
}
.testimonal p{
    margin-top: 14px;
    color: rgb(105, 105, 107);
    transition: 0.2s;
}
.items{
    width: 85%;
    margin: 6px;
}
.testimonal .items:hover{
    background-color: #1554dd;
    color: #fff;
}
.testimonal .items:hover img{
    border:2px solid #fff;
}
.testimonal .items:hover span{
    color: rgb(144, 185, 232);
}
.testimonal .items:hover p{
    color: rgb(195, 197, 202);
}
.testimonal .items:hover i{
    background-color: #fff;
    color: #1554dd;
}
@media screen and (max-width:872px) {
    .testimonal .flex {
            /* padding: auto; */
            align-items: left;
            justify-content: left;
            border-radius: 10px 0;
            transition: 0.5s;
    }
    .testimonal .items{
        max-width:100%;
    }
    .testimonal .name span {
        font-size: 16px;
    }
}
@media screen and (max-width:545px) {
    .testimonal .flex{
        flex-direction: column;
    }
    .testimonal img {
        width: 80px;
        height: 80px;
    }
    .testimonal .name h2{
        font-size: 20px;
        text-align: center;
    }
    .testimonal .name span {
        font-size: 14px;
    }
    .testimonal i {
        left:50px
    }
    .testimonal p{
        font-size: 15px;
    }
    .testimonal .name{
        margin:18px;
    }
}
