.coursesCard{
    padding:50px 0;   
    line-height: 26px;
}
.coursesCard .container .grid2{
    justify-content: center;
    align-items: center;
    position: relative;
}
.left{
    z-index: 1;
    position: relative;
    width:0;
    right:32px;
    bottom: 38px;
}
.coursesCard .items{
    background-color: #fff;
    padding: 9px 10px 9px 0;
    text-align: center;
    text-transform: capitalize;
    border-radius: 5px;
    margin: 10px 6px;
    transition: 0.4s;
    box-shadow: 0 5px 25px -2px rgba(0 0 0 /6%);
}
.coursesCard .img{
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background-color: #f6f8ff;
    padding: 10px;
    border: 2px solid #1544dd;
}
.coursesCard img{
    width:100%;
    height: 100%;
    object-fit: cover;
}
.coursesCard .text{
    margin-left: 10px;
    text-align: left;
}
.coursesCard .text h1{
    font-size: 24.3px;
    font-weight: 500;
    line-height: 35px;
    text-transform: uppercase;
    text-align: center;
}
.coursesCard .text h4{
    letter-spacing: 1.1px;
    font-size: 17px;
    margin-left: 10px;
}
.coursesCard .details .dimg img{
    width: 100%;
    height: 200px;
    min-width: 240px;
    border-radius: 5px;
}
.coursesCard .details .box{
    /* display: flex; */
    align-items: center;
    color: rgb(70, 70, 71);
}
.coursesCard .details span{
    color: #1554dd;
    font-weight: 500;
    font-style: 14px;
}
.grid2 .items{
    width:275px;
    border-top:2px solid #1544dd;
    border-left: 2px solid #1544dd;
}
.coursesCard .items:hover,.items:focus {
    background-color: #d9e1fb;
}
.coursesCard .items:hover .img{
    background-color: #d9e1fb;
}
.flex:hover{
   background-color: none;
}
.online{
    text-align: center;
    padding-bottom:10px ;
}
.online .box{
    box-shadow: 0 5px 25px -2px rgba(0 0 0 /6%);
    background-color: #fff;
    color: #1544dd;
    padding: 20px 20px;
    transition: 0.5s;
    width:30%;
    border-radius: 4px;
}
.online .box:hover{
    background-color: rgba(235, 236, 238, 0.996);
}
.online .img{
    width:80px;
    height:80px;
    align-items: center;
    margin: auto;
}
.online .img i{
    font-size: 80px;
    box-shadow: 0 5px 25px -2px rgba(0 0 0 /6%);
}
.online h1{
   color:rgb(51, 51, 51);
   padding: 10px 0 3px;
   font-weight: 500;
   line-height: 25px;
}
.online span{
    color: grey;
}
@media screen and (max-width:872px) {
    .left {
        bottom: 165px;
        left: 37%;
    }
    .grid2 .items{
        width:275px;
        border-top:2px solid #1544dd;
        border-left: 0;
        border-bottom: 2px solid #1544dd;
    }
}
@media screen and (max-width:826px) {
    .marigin {
       margin-top: 73%;
    }
    .list h1{
        font-size: 24px;
    }
}
@media screen and (max-width:672px){
    .online .box {
        width:100%;
        border-radius:0;
    }
    .online .img{
    width:50px;
    height:50px;
    }
    .online .img i{
       font-size: 50px;
    }
    .marigin{
       margin-top: 100vh;
   }
}
