.contact .left{
    left:0;
    right:0;
    top:0;
    bottom: 0;
    width: 50%;
}
.contact iframe{
    width: 100%;
    height: 100%;
    border:none;
    /* min-width: 300px; */
}
.contact .right{
    padding: 40px 35px;
}
.contact h1{
    font-size: 30px;
    font-weight: 400;
}
.contact p{
    margin:25px 0;
}
.contact .box p{
    font-style: 17px;
    margin-top: 8px;
}
.contact textarea,
.contact input{
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    outline: none;
    border: 1px solid grey;
    color: grey;
    border-radius: 3px;
    font-size: 16px;
    font-weight: 500;
    letter-spacing: 0.5px;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.contact textarea:focus,
.contact input:focus {
    border: 1px solid #1554dd;
    background-color: #eceff7e4;
}
.contact .flexSB input:nth-child(1){
    margin-right: 10px;
}
.contact h3{
    margin-top: 20px;
    font-weight: 500;
    margin-bottom: 8px;
}
/* .contact span{
    color: #1554dd;
    font-weight: 600;
    font-size: 15px;
    word-spacing: 5px;
} */
.contact i{
    margin: 0 2px;
    border: 1px solid #1554dd;
}
@media screen and (max-width:768px) {
    .contact .row{
        width: 100%;
    }
    .contact .container{
        flex-direction: column;
    }
    .contact iframe{
        width: 100%;
        height: 50vh;
    }
}