.newletter{
    background-color: #1554dd;
    margin-top: 50px;
    padding: 50px 0;
    color: #fff;
    background-image: url(../../../../public/images/event.jpg);
}
.newletter .left{
    left:0;
    right:0;
    top:0;
    bottom: 0;
    width: 50%;
}
.newletter .left,
.newletter .right{
    padding-right: 40px;
}
.newletter h1{
    color: #fff;
    font-size: 30px;
    font-weight: 500;
    margin-bottom: 10px;
}
.newletter .right{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.newletter input{
    width:100%;
    padding: 20px;
    border: none;
    outline: none;
    height: 30px;
}
.newletter i{
    padding: 12px;
    color: #1554dd;
    background-color: #fff;
    border-left: 1px solid grey;
}
.newletter .left span{
    color: rgb(180, 191, 204);
}
@media screen and (max-width:786px){
    .row{
        width:100%;
    }
    .newletter .left{
        margin-bottom: 20px;
        width: 100%;
    }
    .newletter .container{
        flex-direction: column;
    }
}
/* ======================= Footer Main ===================== */
footer .container{
    border-bottom: 2px solid #fff;
    max-width: 100%;
}
.footerBox{
    display: flex;
    flex-direction: row;
    width:100%;
    justify-content: space-evenly;
    margin: 0;
}
footer .box{
    width: 32%;
    align-items: center;
    text-align: center;
    justify-content: center;
    padding-top: 40px;
    padding-bottom: 20px;
}
.imgLogo{
    display: flex;
    align-items: center;
    margin: auto;
    justify-content: center;
}
.imgLogo img{
    width:210px;
    height:210px;
}
.logo img{
    width:270px;
}
.logo i{
    border: 1px solid #1554dd;
    margin: 20px 5px 10px;
}
.ficon{
    display: flex;
    justify-content: center;
}
.newletter .right i:focus,
.newletter .right i:hover{
   background-color: #1554dd;
   border: 1px solid #fff;
   color: #fff;
   padding: 11px;
}
.mlogo{
    text-align: center;
    background-color: #1554dd;
}
.mlogo h2{
    font-size: 20px;
    color: #fff;
}
.mlogo p{
    font-size: 15px;
    color:#d4e1eb;
}
.box.text h2{
   font-size: 30px;
}
.box.text p{
    letter-spacing: 0.5px;
    padding-top: 8px;
    font-size: 19px;
    color: #1554dd;
    transition: 0.5s;
}
.box.text p:hover {
    color:grey;
}
@media screen and (max-width:992px) {
    .footerBox {
        flex-direction: column;
        width: 100%;
        justify-content: center;
    }
    footer .box {
        width: 100%;
        padding-top: 20px;
    }
    .logo img{
        width:fit-content;
        height:30vh;
    }
    .box.text p{
    letter-spacing: 0;
    padding-top: 4px;
    font-size: 18px;
    color: #1554dd;
    }
    .ficon{
        display: none;
    }
}

/* ========================= copyright =================== */
.copyright{
    padding: 12px;
    text-align: center;
    color: #fff;
    background-color: #1554dd;
}