/* ---------------- Header ---------------- */
header{
    background-color: rgba(255,255,255,0.3);
    margin: 0 30px;
    position: relative;
}
header ul{
    padding: 20px 20px;
}
header ul li{
    list-style: none;
    margin-right: 20px;
}
header ul li a{
    color:#fff;
    font-weight:500;
    transition: 0.5s;
}
header ul li a:hover{
   color: #1554dd;
}
.start{
    background-color: #1554dd;
    padding: 20px 42px;
    color:#fff;
    transition: 0.5s;
    clip-path: polygon(10% 0,100% 0,100% 100%,0 100%);
}
.start:hover{
    background-color: #2969f3;
}
a{
    text-decoration: none;
}
a:hover{
    color: #1554dd;
}
.toggle{
    display:none;
}
@media screen and (max-width:712px) {
    .start{
        clip-path: none;
    }
    header {
        margin: 0;
        background-color: #1554dd;
        position: relative;
    }
    header ul.flexSB{
        display: none;
    }
    header ul li{
        padding: 10px;
        text-align: center;
        transition: 0.8s;
    }
    header ul li:hover {
       background-color: #417bfa;
    }
    header ul li a:hover{
       color: #fff;
    }
    .toggle{
        display: block;
        background: none;
        border: none;
        color:#fff;
        font-size: 30px;
        position: absolute;
        right: 20px;
        top:-30px;
        transition: 0.5s;
    }
    .mobile-nav{
        position: absolute;
        top:9vh;
        left:0;
        width:100% ;
        background-color: #1554dd;
    }
}
@media screen and (max-width:646px) {
    .head .social{
        position: fixed;
        display: flex;
        flex-direction: column;
        top:36%;
        right:0;
        background-color: rgba(255, 255, 255, 0.56);
        padding: 0;
        margin: 0;
        justify-content: left;
        border-left: 1px solid #1554dd;
    }
    .head .icon {
        margin-left: 0;
        border-radius: 0;
        background-color: rgba(255, 255, 255, 0);
        border-bottom: 1px solid #1554dd;
        border-top: 1px solid #1554dd;
    }
    .head .icon:hover{
        background-color: #1554dd;
    }
    .logo{
        text-align: center;
    }
    .head .flexSB{
        justify-content: center;
    }
}
@media screen and (max-width:464px){
    .logo h1{
        font-size: 24px;
    }
    .mobile-nav{
        top:6.6vh;
    }
}
ul li{
    color: #fff;
    transition: 0.5s;
}
ul li:hover{
    color: #2666f0;
}
.mobile-nav li:hover{
    color: #fff;
}
.start .button{
    color: #fff;
}