.blog img{
    width: 100%;
    height: 100%;    
}
.blog .text{
 padding: 25px 25px;
}
.blog h1{
    font-weight: 500;
    font-size: 22px;
    line-height: 30px;
    margin: 20px 0;
    transition: 0.5s;
}
.blog h1:hover {
    color:#1554dd;
    cursor: pointer;
}
.blog span{
    text-transform: uppercase;
    font-weight: 500;
    color: #1554dd;
    display: block;
}
.blog span label{
    margin-left: 5px;
    color: grey;
    font-size: 14px;
}
.blog span:nth-last-child(1) label{
    color: #1554dd;
}
.blog p{
    color: grey;
}
.blog .items{
    max-width: 320px;
}
.blog .grid3{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}
.admin .flexSB{
    display: flex;
    flex-wrap: wrap;
}
.admin a h1{
    color: black;
}
@media screen and (max-width:482px){
    .blog span:nth-last-child(1) {
        display: none;
    }
}