@import url('https://fonts.googleapis.com/css2?family=Arima:wght@200&family=Charm:wght@700&family=Quicksand:wght@500&family=Roboto:ital,wght@0,300;0,400;0,500;0,700;0,900;1,300;1,400;1,500;1,700;1,900&family=Signika+Negative:wght@400;500;600;700&family=Signika:wght@400;500;600;700&display=swap');
*{
  padding:0;
  margin:0;
  box-sizing: border-box;
}
body{
  font-family:"Roboto",sans-serif;
  background-color: #f8f8f8;
}
.container{
  max-width: 85%;
  margin:auto;
}
.flexSB{
  display: flex;
  justify-content: space-between;
}
.icon{
  width:40px;
  height:40px;
  line-height: 40px;
  border-radius:50%;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.45);
  color:#1554dd;
  transition: 0.5s;
}
.icon:hover{
   cursor:pointer;
   background-color: #1554dd;
   color: #fff;
   inset:1px 1px #fff;
}
button{
  border: none;
}
.row{
  width:50%;
}
#heading {
  text-align: center;
  padding: 40px 0;
}

#heading h3 {
  font-weight: 600;
  letter-spacing: 1px;
  color: #1554dd;
  text-transform: uppercase;
}

#heading h1 {
  font-size: 45px;
  margin: 16px 0;
  text-transform: capitalize;
}

p {
  line-height: 30px;
  font-size: 18px;
}
.back{
  background-image: url("../public/cover.jpg");
  background-size: cover;
  background-attachment: fixed;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100vh;
  padding-top: 30%;
  padding-right: 40px;
  color: #fff;
  text-align: center;
}
.back h1{
  font-size: 100px;
  font-weight: 400;
}
.back h2{
  font-weight: 500;
  font-size: 17px;
  text-transform: uppercase;
}
.marigin{
  /* margin-top: 40.3%; */
  margin-top: 100vh;
}
.grid{
  display: grid;
  grid-template-columns: repeat(4,1fr);
  grid-gap:30px;
}
.flex{
  display: flex;
  cursor: pointer;
}
.grid2{
  display: flex;
  /* grid-template-columns: repeat(3,1fr); */
  justify-content: space-around;
  grid-gap:26px;
  flex-wrap: wrap;
}

.outline-btn{
  margin:auto 10px 4px 6px;
  box-shadow: none;
  border:1px solid #1554dd;
  background-color: #fff;
  color: #1554dd;
  margin-top: 8px;
  padding: 6px;
  width:100%;
  transition: 0.5s;
}
.outline-btn:hover{
  background-color: #1554dd;
  color: #fff;
}
.grid3{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  width:100%;
  height: fit-content;
}
.padding{
  padding: 60px 0;
}
.shadow{
  box-shadow: 0 5px 25px -2px rgba(0 0 0 /6%);
  background-color: #fff;
}
@media screen and (max-width:872px){
  .grid{
    grid-template-columns: repeat(2, 1fr);
    grid-gap:auto;
    padding: 10px 0;
  }
  .flex{
    padding: auto;
    align-items: center;
    justify-content: center;
    border-radius: 10px 0;
    transition: 0.5s;
  }
  .back{
    background-position: center;
    padding-top: 50vh;
    padding-right: 0;
    padding: auto;
    height: 100vh;
  }
  /* .grid2{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap:15px;
  } */
    .marigin{
      margin-top: 100vh !important;
  }
}
@media screen and (max-width:672px) {
  .grid3 {
    flex-direction: column;
  }
  #heading h1 {
    font-size: 40px;
  }
  .marigin{
      margin-top: 100vh !important;
  }
}
@media screen and (max-width:484px) {
  .flex{
    border-radius: 0;
  }
  #heading h1 {
    font-size: 35px;
  }
  .marigin{
      margin-top: 100vh;
  }
}
.headText{
  text-align: center;
  /* margin: 10px auto 0; */
  padding: 10px;
  background-color: #1554dd;
  color: #fff;
  text-transform: uppercase;
  font-size: 36px;
  letter-spacing: 1px;
}
button{
  cursor:pointer;
}