.teamHead h1{
    display: block;
    background-color: #1554dd;
    color: #fff;
}
.grid4{
    display: flex;
    flex-wrap: wrap;
}
.team .items{
transition:0.5s;
width: 235px;
margin: 10px auto;
border-radius: 5px;
}
.team .img{
 position: relative;  
}
.team img{
    width:100%;
    height: 215px;
    border-radius: 5px 5px 0 0;
    /* object-fit:cover; */
}
.overlay{
    position: absolute;
    bottom: 85px;
    left:-9px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 10px;
    transition: 0.5s;
    opacity: 0;
}
.overlay i{
    background-color: #fff;
    color: rgb(37, 97, 227);
    margin: 5px;
}
.team .details{
padding: 12px;
text-align: center;
}
.team .details h2{
    font-weight: 500;
    font-size: 20px;
    transition: 0.5s;
}
.team .details p{
    font-size: 16px;
    color: grey;
    margin-top: 10px;
    transition: 0.5s;
}
.team .items:hover{
    background-color: #1554dd;
    color: #fff;
    cursor: pointer;
}
.team .items:hover h2{
    color: rgb(255, 255, 255);
}
.team .items:hover p {
    color: rgb(213, 213, 213);
}
.team .items:hover .overlay{
    opacity: 1;
}