.hero{
    background-image:url("../../../../public/cover2.jpg");
    background-size: cover;
    background-attachment: fixed;
    position: absolute;
    top:0;
    left:0;
    z-index: -1;
    width: 100%;
    height: 100vh;
    padding-top: 20%;
    padding-right: 40px;
    color: #fff;
}
button{
  padding: 12px 20px;
  background-color: #1554dd;
  font-weight: 600;
  font-size: 16px;
  color:#fff;
  transition: 0.5s;
  margin: 30px 10px 0 0;
  border-radius: 3px;
  cursor: pointer;
  box-shadow: 0 24px 36px -11px rgba(0 0 0 /9%);
}
button i{
    margin-left: 10px;
    cursor: pointer;
}
button:hover{
    box-shadow: none;
}
.primary-btn {
    background-color: #fff;
    color: #1554dd;
    cursor: pointer;
}

.hero #heading h3{
    color: #fff;
}
.hero #heading{
    text-align: left;
    margin: 0;
    padding: 0;
}

@media screen and (max-width:892px) {
    .margin{
        height: 40vh;
    }
    .hero{
        background-position: right;
        padding-top: 45%;
        height: 100vh;
    }
    .hero .row{
        width: 100%;
    }
    /* .hero #heading h1{
       color: #1554dd;
    } */
}