.faq .container{
    max-width: 70%;
    border-radius: 5px;
}
.faq .box{
    background-color:#fff ;
    margin-bottom: 20px;
}
.faq button{
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 15px 20px;
    background-color: #fff;
    color:#1554dd;
    font-size: 14px;
    text-align: left;
    align-items: center;
    font-weight: 300;
    letter-spacing: 0.4px;
}
.faq h2{
    font-weight: 500;
}
.faq p{
    padding: 20px;
    background-color: rgb(235, 239, 248);
}
.faq .accordion{
    transition: 0.5s;
}
.faq .accordion:hover{
   background-color: #1554dd;
   color: #fff;
}